-left: 10px;\n  list-style-type: none;\n  display: inline-block;\n  font-weight: bold;\n}\n\n.nav-menu ul li a {\n  color: white;\n  text-decoration: none;\n  min-height: 44px;\n  min-width: 44px;\n  display: inline-block;\n  line-height: 44px;\n  padding: 0.9rem;\n\n}\n\n.nav-menu ul li a:hover {\n  transition: ease-out;\n  transition-duration: 0.2s;\n  color: black;\n}\n\n/* hero */\n\n.hero {\n  position: relative;\n  background-image: url('../public/images/sharp/hero-image_4-large.jpg');\n  width: 100%;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  opacity: 0.9;\n}\n\n.hero .inner {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  text-align: center;\n}\n\n.hero h1 {\n  color: #FBFBFB;\n  font-weight: 600;\n  font-size: 3rem;\n}\n\n.hero h2 {\n  color: #FBFBFB;\n  margin-top: 10px;\n  font-weight: 100;\n  font-size: 1.5rem;\n  font-style: italic;\n}\n\n/* Content */\nsection {\n  margin-top: 40px;\n}\n\na,\np,\nh1,\nh2,\nh3,\nh4 {\n  text-decoration: none;\n}\n\n.main-resto_container {\n  margin: auto;\n  width: 100%;\n  margin-top: 1em;\n}\n\n.main-resto_label {\n  margin: auto;\n  width: max-content;\n  font-size: 22px;\n}\n\n.list-resto {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-between;\n  margin: 30px 0;\n}\n\n.resto-item {\n  width: calc(100% / 3 - 16px);\n  margin-bottom: 32px;\n  display: flex;\n  flex-direction: column;\n  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 6px 0px;\n  border-radius: 10px;\n  -webkit-border-radius: 10px;\n  -moz-border-radius: 10px;\n  -ms-border-radius: 10px;\n  -o-border-radius: 10px;\n}\n\n.resto-title {\n  display: flex;\n  justify-content: space-between;\n}\n\n.resto-item_image {\n  object-fit: cover;\n  width: 100%;\n  height: 250px;\n  border-radius: 10px 10px 0 0;\n}\n\n.resto-item_name {\n  background-color: #62D2A2;\n  color: white;\n  margin: 1em;\n  text-align: center;\n  border-radius: 20px;\n  font-weight: bold;\n  font-size: 24px;\n  transition: 0.3s opacity;\n  line-height: 44px;\n}\n\n.card-location-rating {\n  columns: 2;\n  justify-content: center;\n  margin-bottom: 5px;\n}\n\n.card-city {\n  color: black;\n  text-align: right;\n}\n\n.card-city i {\n  color: red;\n}\n\n.rating {\n  color: black;\n  text-align: left;\n}\n\n.rating i {\n  color: #FEDB39;\n}\n\n.resto-item_name:hover {\n  opacity: 0.7;\n}\n\n.resto-item_desc {\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 5;\n  font-weight: 500;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  margin: 16px 15px;\n  font-size: 14px;\n  line-height: 1.5em;\n  color: black;\n}\n\n/* Footer */\nfooter {\n  text-align: center;\n  width: 100%;\n  background-color: #62D2A2;\n  padding: 20px 0;\n  line-height: 3em;\n\n}\n.footer-text {\n  color: white;\n  font-weight: bold;\n}\n\n#root {\n  padding-top: 65px;\n}\n\n/*Detail*/\n\n.restaurant {\n  width: 100%;\n}\n\n.restaurant-detail__menu-list {\n  margin-bottom: 100px;\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n  gap: 50px;\n  max-width: 750px;\n  margin: 0 auto;\n  padding-bottom: 50px;\n}\n\n.restaurant-detail__foods li,\n.restaurant-detail__drinks li {\n  list-style: none;\n  margin: 10px 15px;\n  padding: 1px;\n  color: black;\n  text-align: left;\n}\n\n.restaurant-detail__drinks i{\n  color: #62D2A2;\n  margin-right: 10px;\n}\n\n.restaurant-detail__foods i{\n  color: #62D2A2;\n  margin-right: 10px;\n}\n\n.foods,\n.drinks {\n  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,              rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n  padding-top: 10px;\n  border-radius: 10px;\n}\n\n.detail {\n  display: grid;\n  grid-gap: 1.545em;\n  font-size: 1em;\n  margin-top: 10px;\n  text-align: center;\n}\n\n.detail-img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  object-position: center;\n}\n\n.detail-name-address-rating {\n  display: inline-block;\n  margin: 0;\n}\n\n.icon-primary {\n  color: black;\n}\n\n.detail-desc {\n  text-align: justify;\n  margin-bottom: 0.5em;\n  margin: 0;\n  font-size: 14px;\n  line-height: 1.5em;\n  font-weight: 400;\n}\n\n.category {\n  padding: 0.3em 1em;\n  color: white;\n  background-color: #62D2A2;\n  margin-right: 2px;\n  border: 1px solid transparent;\n  border-radius: 5px 5px 5px 5px;\n  text-align: left;\n  height: 50px;\n  font-weight: bold;\n}\n\n.detail-info {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n  margin-bottom: 0;\n  margin-right: 20px;\n}\n\n#resto-detail {\n  margin-top: 30px;\n  ;\n}\n\n.restaurant-item__not__found {\n  margin-top: 30px;\n  text-align: center;\n}\n\n.resto-item_image-container {\n  border-radius: 10px;\n}\n\n.img-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.detail-info h4 {\n  padding-top: 20px;\n  color: #62D2A2;\n  font-size: 1.1em;\n  text-align: left;\n  margin-top: 10px;\n}\n\n.detail-info li {\n  position: relative;\n  display: block;\n  margin-top: 10px;\n}\n\n.detail-info .resto-name {\n  font-size: 18px;\n  text-align: left;\n}\n\n.detail-info .resto-name p {\n  font-size: 1.4em;\n  font-weight: bold;\n  color: #62D2A2\n}\n\n.detail-info .resto-address p {\n  font-size: 1.1em;\n  font-weight: bold;\n}\n\n.detail-info .resto-address i {\n  color: red;\n}\n\n.detail-info .resto-rating p {\n  font-size: 1.1em;\n  font-weight: bold;\n}\n\n.detail-info .resto-rating i {\n  color: #FEDB39;\n}\n\n.resto-address,\n.resto-rating,\n.resto-category {\n  font-size: 14px;\n  margin: 0;\n  text-align: left;\n}\n\n.explore-restaurant__label {\n  font-size: 32px;\n  font-weight: bold;\n  text-align: center;\n  color: black;\n  padding-top: 30px;\n}\n\n/* Detail Review */\n.detail-review {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  gap: 20px;\n  max-width: 100%;\n  font-size: 1em;\n  text-align: center;\n}\n\n.detail-review-item {\n  color: black;\n  background-color: white;\n  margin-bottom: 1.5em;\n  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n  border-radius: 10px;\n}\n\n.header-review {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 1em;\n  color: #62D2A2;\n  border-bottom: 1px solid #62D2A2;\n}\n\n.name-review {\n  font-weight: bold;\n  font-size: 1.5em;\n  margin: 0;\n}\n\n.date-review {\n  font-size: 1em;\n  font-weight: lighter;\n  font-size: 1em;\n}\n\n.body-review {\n  padding: 1em;\n  text-align: left;\n  font-size: 1.37em;\n}\n\n/* Like */\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #62D2A2;\n  color: white;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 5px;\n\n  font-size: 18px;\n  cursor: pointer;\n  box-shadow: white 0px 0px 5px 3px;\n}\n\n\n\n"],"sourceRoot":""}]);
